.slogan-container {
    position: absolute;
    display: inline-block;
    width: 40%;
    top: 17rem;
    left: 50%;
    transform: translate(-50%);
    border-radius: 5px;
    text-align: center;
    padding: 10px 20px;
    background-color: rgb(58, 59, 59, 0.7);
    color: white;
    z-index: 0;
}

@media only screen and (max-width: 1360px) {
    
}

@media only screen and (max-width:780px) {
    .slogan-container {
        top: 12rem;
    }
}


/*smaller height*/
@media only screen and (max-height:900px) {
    .slogan-container {
        display: none;
    }
}


